@import "../../styles/main.scss";

.experience__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px;
}

.experience__container > div {
  padding: 38px 80px;
  border-radius: 32px;
  border: 1px solid transparent;
}

.experience__container > div:hover {
  cursor: default;
}

.experience__container > div h3 {
  text-align: center;
  margin-bottom: 32px;
}

.experience__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 32px;
}

.experience__details {
  display: flex;
  gap: 16px;
}

.experience__details-icon {
  margin-top: 6px;
}

.light {
  .experience__container > div {
    background: $light-clr-bg-variant;
  }
  .experience__container > div:hover {
    background: transparent;
    border-color: $light-clr-primary-variant;
  }

  .experience__container > div h3 {
    color: $light-clr-primary;
  }
  .experience__details-icon {
    color: $light-clr-primary;
  }
}
.dark {
  .experience__container > div {
    background: $dark-clr-bg-variant;
  }
  .experience__container > div:hover {
    background: transparent;
    border-color: $dark-clr-primary-variant;
  }

  .experience__container > div h3 {
    color: $dark-clr-primary;
  }
  .experience__details-icon {
    color: $dark-clr-primary;
  }
}

@media screen and (max-width: 1024px) {
  .experience__container {
    grid-template-columns: 1fr;
  }

  .experience__container > d {
    width: 80%;
    padding: 32px;
    margin: 0 auto;
  }

  .experience__content {
    padding: 16px;
  }
}
@media screen and (max-width: 600px) {
  .experience__container {
    gap: 16px;
  }

  .experience__container > div {
    width: 100%;
    padding: 32px 16px;
  }
}
